.circle .traco {
    position: absolute;
    z-index: -99;
}

.loader .content {
    width: 100px;
    height: 100px;
    border-radius: 50%;
    border: 4px solid #ffe71c;
    overflow: hidden;
}

.content .eyes {
    width: 80px;
    height: 30px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin: 18px auto 0px;
    padding: 3.5px 10px;
}

.eyes .eye-left {
    width: 22px;
    height: 100%;
    background-color: #ffe71c;
    border-radius: 50%;
}

.eyes .eye-right {
    width: 22px;
    height: 100%;
    background-color: #ffe71c;
    border-radius: 50%;
}

.content .nouse {
    margin: -4px auto;
    height: 5px;
    width: 5px;
    border-radius: 50%;
    background-color: #ffe71c;
}

.content .mouth {
    margin: 9px auto 0;
    height: 3px;
    width: 100%;
    background-color: #ffe71c;
}

.content .teeth {
    margin: 0 auto;
    height: 37px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 0 12px;
}

.teeth .tooth {
    height: 100%;
    width: 4px;
    background-color: #ffe71c;
}